body {
  padding: 0 1rem; 
}

/* Custom Container Style */
.custom-container {
  max-width: 1200px;
  margin: 0 auto;
  padding: 0 15px;
}

/* Custom Card Style */
.book-card {
  background-color: #b97aba; 
  border-radius: 8px;
  box-shadow: 0 4px 6px -1px rgba(0, 0, 0, 0.1), 0 2px 4px -1px rgba(0, 0, 0, 0.06);
  padding: 20px;
  transition: transform 0.3s ease-in-out, box-shadow 0.3s ease-in-out;
  position: relative; /* For absolute positioning of front and back card */
}

.book-card:hover {
  transform: translateY(-5px);
  box-shadow: 0 10px 15px -3px rgba(228, 95, 95, 0.1), 0 4px 6px -2px rgba(0, 0, 0, 0.05);
}

/* Image styling inside book cards */
.book-card img {
  max-width: 100%;
  height: auto;
  display: block;
  object-fit: cover;
  border-radius: 8px;
}

/* Custom Button Style */
.custom-button {
  background-color: #eb99ed; 
  border: none;
  color: rgb(241, 98, 98);
  padding: 16px 32px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
  margin: 4px 2px;
  transition-duration: 0.4s;
  cursor: pointer;
}

.custom-button:hover {
  background-color: white;
  color: black;
}

/* Custom Typography */
.custom-title {
  font-size: 2rem;
  color: #333;
  font-weight: bold;
}

.custom-subtitle {
  font-size: 1.5rem;
  color: #555;
}

/* Search bar centered */
.search-bar {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 1rem;
  width: 100%;
}

/* Styles for the input field */
.search-bar input {
  flex: 1;
  margin-right: 0.5rem;
  padding: 0.5rem;
}

/* Styles for the button */
.search-bar button {
  padding: 0.5rem 1rem;
  cursor: pointer;
}

/* Responsive Grid */
.book-grid {
  display: grid;
  gap: 1rem;
  grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
  padding: 1rem;
}

@media (min-width: 768px) {
  .book-grid {
    grid-template-columns: repeat(auto-fit, minmax(200px, 1fr));
  }
}

@media (min-width: 992px) {
  .book-grid {
    grid-template-columns: repeat(3, 1fr);
  }
}

@media (min-width: 1200px) {
  .book-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}


/* Responsive container padding adjustment */
@media (max-width: 768px) {
  .custom-container {
    padding: 0 10px; /* Reduces padding on smaller screens */
  }

  .custom-title, .custom-subtitle {
    text-align: center; /* Centers titles on smaller screens */
  }

  .book-grid {
    grid-template-columns: 1fr; /* Stacks the cards in a single column on smaller screens */
  }

  .book-card {
    margin-bottom: 1rem; /* Adds some space between stacked cards */
  }
}

@media (min-width: 768px) {
  .custom-container {
    padding: 0 15px; /* Default padding for medium screens and up */
  }

  /* Adjust the book-grid for medium screens if needed */
  .book-grid {
    grid-template-columns: repeat(auto-fit, minmax(180px, 1fr)); /* Adjusts the number of columns based on screen size */
  }
}

@media (min-width: 992px) {
  /* Optionally, further adjust the grid for larger screens */
  .book-grid {
    grid-template-columns: repeat(3, 1fr); /* For example, 3 columns for large screens */
  }
}

@media (min-width: 1200px) {
  .book-grid {
    grid-template-columns: repeat(4, 1fr); /* For example, 4 columns for extra large screens */
  }
}